@media screen and (max-width: 768px) {
  .m-menu-overlay {
    display: block;
    visibility: hidden;
    opacity: 0;
    transition: 0.25s;
  }
  .m-menu-overlay.visible {
    visibility: visible;
    opacity: 1;
  }
  .m-menu {
    position: fixed;
    height: 100%;
    left: 0;
    top: 0;
    background: #2a2a2a;
    z-index: 10;
    transform: translateX(-100%);
    transition: 0.25s;
  }
  .m-menu__close {
    display: flex;
  }
  .m-menu__icon {
    fill: #ffffff;
  }
  .m-menu__items {
    flex-direction: column;
  }
  .m-menu__item {
    color: #ffffff;
    border-radius: 0;
    border: none;
  }
  .m-menu__item:not(:first-child) {
    margin-left: 0;
  }
  .m-menu.visible {
    transform: translateX(0%);
  }
  .m-menu.visible:after {
    display: none;
  }
  .m-menu:after {
    position: absolute;
    height: 100%;
    right: 0;
    width: 15px;
    content: '';
    top: 0;
    transform: translateX(100%);
  }
