.o-filter-btn-wrap {
  display: none;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.o-filter-btn {
  width: calc(100% - 30px);
  margin: auto;
}

.o-filter {
  position: relative;
  margin-top: 40px;
}

.o-filter__close {
  display: none;
  width: calc(100% - 30px);
  margin: 15px auto;
}

.o-filter__form {
  box-shadow: 3px 4px 10px 0px rgba(0, 0, 0, 0.2);
  background: #ffffff;
  padding: 30px 20px;
  border-radius: 2px;
}

.o-filter__row {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.o-filter__row:not(:last-child) {
  margin-bottom: 20px;
}

.o-filter__col {
  width: 25%;
  padding: 5px 15px;
}

.o-filter--min .o-filter__col {
  width: 33.3333337%;
}

.o-filter--min .o-filter__col--state, .o-filter--min .o-filter__col--realty {
  display: none;
}

.o-filter--second .o-filter__col--state {
  display: none;
}

.o-filter--second .o-filter__col--range-1, .o-filter--second .o-filter__col--range-2, .o-filter--second .o-filter__col--search {
  width: 33.3333337%;
}

.o-filter--moved {
  margin-top: -35px;
}

@media screen and (max-width: 992px) {
  .o-filter:not(.o-filter--min) .o-filter__row:not(:last-child) {
    margin-bottom: 0;
  }
  .o-filter:not(.o-filter--min) .o-filter__col {
    width: 50%;
    margin-bottom: 15px;
  }
  .o-filter:not(.o-filter--min) .o-filter__col--state {
    order: 1;
  }
  .o-filter:not(.o-filter--min) .o-filter__col--range-1 {
    order: 3;
  }
  .o-filter:not(.o-filter--min) .o-filter__col--range-2 {
    order: 4;
  }
  .o-filter:not(.o-filter--min) .o-filter__col--search {
    order: 2;
  }
  .o-filter--second .o-filter__col--range-1, .o-filter--second .o-filter__col--range-2, .o-filter--second .o-filter__col--search {
    width: 100% !important;
  }
  .o-filter--second .o-filter__col--range-1 {
    order: 2 !important;
  }
  .o-filter--second .o-filter__col--range-2 {
    order: 3 !important;
  }
  .o-filter--second .o-filter__col--search {
    order: 4 !important;
  }
}

@media screen and (max-width: 768px) {
  .o-filter-btn-wrap {
    display: block;
  }
  .o-filter {
    display: none;
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    padding: 0;
    margin: 0;
    height: 100%;
    overflow-y: auto;
    background: #2a2a2a;
  }
  .o-filter__close {
    display: flex;
  }
  .o-filter .o-filter__form {
    box-shadow: none;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  .o-filter .o-filter__col {
    width: 100%;
    margin-bottom: 15px;
  }
  .o-filter .o-filter__row:not(:last-child) {
    margin-bottom: 0;
  }
  .o-filter:not(.o-filter--min) .o-filter__col {
    width: 100%;
  }
  .o-filter:not(.o-filter--min) .o-filter__col--state {
    order: 1;
  }
  .o-filter:not(.o-filter--min) .o-filter__col--range-1 {
    order: 2;
  }
  .o-filter:not(.o-filter--min) .o-filter__col--range-2 {
