// add-advert styles
.m-add-advert {
  cursor: pointer;
  display: flex;
  align-items: center;
  &__text {
    font-size: $fs-12;
    text-transform: uppercase;
    transition: color $t-normal;
    margin-right: 7px;
  }
  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background-color: $c-orange;
    border-radius: 50%;
    box-shadow: $bs-main;
    transition: box-shadow $t-normal;
  }
  &__icon {
    fill: $c-white;
    width: 16px;
    height: 16px;
    transition: transform $t-normal;
  }

  &:hover,
  &.active {
    .m-add-advert__icon {
      transform: scale(1.3)
    }
    .m-add-advert__btn {
      box-shadow: none;
    }
    .m-add-advert__text {
      color: $c-orange;
    }
  }
}

//------------------------------
// Responsive
//------------------------------
@media #{$desktop} {
  .m-add-advert {
    &__text {
      display: none;
    }
  }
}

@media #{$tablet} {
  .m-add-advert {
    &__text {
      display: block;
    }
  }
}
@media #{$phone} {
  .m-add-advert {
    &__text {
      display: none;
    }
  }
}